.header-menu{
  transform: translateY(-120%);
  z-index: 2;
  opacity: 0;
  position: fixed;
  background: $color-default-white;
  width: 100%;
  left: 0;
  top: 69px;
  transition: transform $trans-default, opacity $trans-default;;
  pointer-events: none;


  &.is-active{
    transform: translateY(0px);
    opacity: 1;
    pointer-events: all;
  }
}

.header-menu__wrapper{
  width: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 20px;

}

.header-menu__top-block{
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 40px;
  max-width: 1150px;
  :last-child{
    border: none;
    grid-template-columns: 80px;
  }
}

.header-menu__brand{
  font-family: 'Inter';
  font-style: normal;
  font-weight: 300;
  font-size: 12px;
  line-height: 15px;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: $color-arapawa;
}

.header-menu__bottom-block{
  padding: 20px 0px;
  max-width: 521px;
  width: 100%;
  display: flex;
  justify-content: space-between;

  position: relative;

  a{
    transition: color $trans-default;

    @include hover {
    color: rgb(81, 140, 165);
  }
}


&::after{
  content: ' ';
  height: 1px;
  width: 1541px;
  background-color: #eae9e8;
  top: 0;
  right: -100%;
  bottom: 0;
  position: absolute;
}

}

.header-menu__menu{
  display: none;

}

.header-menu__menu-right-block{
  &--bottom{
    display: none;
  }
}


.header-menu__column{
  max-width: 230px;
  padding: 0px 15px 44px;
  border-right: 1px solid #EAE9E8;
}

.header-menu__title{
  font-family: 'Inter';
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 17px;
  color: $color-blue-whale;
  margin: 45px 0px 30px;
  text-transform: uppercase;
}

.header-menu__item{
  font-family: 'Inter';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  list-style-type: none;
  margin-bottom: 8px;
  position: relative;
  text-transform: none;

  a{
    color: $color-blue-whale;
    transition: color $trans-default;
  }

  @include hover-focus {
      a{
        color: $color-cinnabar;
      }

      &::before{
        content: '';
        width: 6px;
        height: 6px;
        border-radius: 50%;
        background-color: $color-cinnabar;
        position: absolute;
        top: 32%;
        left: -10%;
      }
  }
}


